import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subject, takeUntil } from 'rxjs';
import { ICustomer } from '../../interfaces/auth.interface';
import { CustomerAuthService, UpdateCustomerDto } from '../../services/customer-auth.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { environment } from '../../config/environment';

@Component({
  selector: 'app-customer-profile',
  templateUrl: './customer-profile.component.html',
  styleUrls: ['./customer-profile.component.scss'],
  standalone: false
})
export class CustomerProfileComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  user: ICustomer | null = null;
  loading = false;
  showLeftPanel = false;

  // ì±„íŒ… ìœ„ì ¯ìš© organizationId
  chatOrganizationId = environment.chatOrganizationId;

  constructor(
    private customerAuthService: CustomerAuthService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    // CustomerAuthServiceì—ì„œ ì‚¬ìš©ì ì •ë³´ ê°€ì ¸ì˜¤ê¸° (ê°€ë“œì™€ ë™ì¼)
    this.customerAuthService.getNullableUser$()
      .pipe(takeUntil(this.destroy$))
      .subscribe(user => {
        this.user = user;
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  toggleLeftPanel(): void {
    this.showLeftPanel = !this.showLeftPanel;
  }

  closeLeftPanel(): void {
    this.showLeftPanel = false;
  }

  onLeftAction(action: any): void {
    this.closeLeftPanel();
  }

  onUpdateProfile(updateData: UpdateCustomerDto): void {
    if (!this.user) return;

    this.loading = true;
    this.customerAuthService.updateProfile(updateData)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (updatedUser) => {
          this.loading = false;
          this.user = updatedUser;
          this.snackBar.open('í”„ë¡œí•„ì´ ì„±ê³µì ìœ¼ë¡œ ì—…ë°ì´íŠ¸ë˜ì—ˆìŠµë‹ˆë‹¤.', 'ë‹«ê¸°', {
            duration: 3000,
            horizontalPosition: 'center',
            verticalPosition: 'top'
          });
        },
        error: (error) => {
          this.loading = false;
          this.snackBar.open('í”„ë¡œí•„ ì—…ë°ì´íŠ¸ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.', 'ë‹«ê¸°', {
            duration: 3000,
            horizontalPosition: 'center',
            verticalPosition: 'top',
            panelClass: ['error-snackbar']
          });
        }
      });
  }
}
