  /**
   * ìŠ¤í† ì–´ì˜ ìƒí’ˆ ëª©ë¡ ë¡œë“œ
   */
  private loadStoreProducts(): void {
    if (!this.storeId) return;

    this.loadingProducts = true;
    
    // ìµœì†Œ ë¡œë”© ì‹œê°„ ë³´ì¥ (shimmer íš¨ê³¼ë¥¼ ë³´ì—¬ì£¼ê¸° ìœ„í•´)
    const minLoadingTime = 300; // 0.3ì´ˆ
    const startTime = Date.now();
    
    this.productApiService.getProductsByStoreId$(this.storeId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (products) => {
          const elapsedTime = Date.now() - startTime;
          const remainingTime = Math.max(0, minLoadingTime - elapsedTime);
          
          setTimeout(() => {
            // isVisibleì´ trueì¸ ìƒí’ˆë§Œ í•„í„°ë§
            this.products = products.filter(product => product.isVisible === true);
            this.loadingProducts = false;
            console.log(`ìŠ¤í† ì–´ ${this.store?.name}ì˜ ë…¸ì¶œ ìƒí’ˆ ${this.products.length}ê°œ ë¡œë“œë¨ (ì „ì²´: ${products.length}ê°œ)`);
          }, remainingTime);
        },
        error: (error) => {
          const elapsedTime = Date.now() - startTime;
          const remainingTime = Math.max(0, minLoadingTime - elapsedTime);
          
          setTimeout(() => {
            this.products = [];
            this.loadingProducts = false;
          }, remainingTime);
        }
      });
  }

