  /**
   * ìƒí’ˆ ì´ë¯¸ì§€ URL ê°€ì ¸ì˜¤ê¸°
   */
  getProductImageUrl(product: IProduct): string {
    // thumbnailMeta ìš°ì„  í™•ì¸ (IUploadImage ê°ì²´)
    const imageMeta = product.thumbnailMeta || product.imageMetas?.[0];
    
    return getImageOrFallback(
      imageMeta,
      product.name,
      undefined, // fallbackìœ¼ë¡œ ìƒì„±
      400
    );
  }

  /**
   * ìŠ¤í† ì–´ ë¡œê³  ì´ë¯¸ì§€ URL ê°€ì ¸ì˜¤ê¸°
   */
