  /**
   * íŒë§¤ì ê¶Œí•œ í™•ì¸
   */
  canCreateProduct(): boolean {
    const currentMode = this.authModeService.getCurrentMode();
    const isSellerAuthenticated = this.sellerAuthService.isAuthenticated();
    return currentMode === 'SELLER' && isSellerAuthenticated;
  }

  /**
   * ìƒí’ˆ ë“±ë¡ ë‹¤ì´ì–¼ë¡œê·¸ ì—´ê¸°
   */
  openCreateProductDialog(): void {
    console.log('canCreateProduct:', this.canCreateProduct());
    
    if (!this.canCreateProduct()) {
      alert('íŒë§¤ì ëª¨ë“œë¡œ ì „í™˜ í›„ ì´ìš© ê°€ëŠ¥í•©ë‹ˆë‹¤.');
      return;
    }

    if (!this.storeId) {
      alert('ìŠ¤í† ì–´ ì •ë³´ë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    // íŒë§¤ì í† í°ì—ì„œ userId ì¶”ì¶œ
    const seller = this.sellerAuthService.getCurrentUser();
    const userId = seller?.id;
    
    if (!userId) {
      alert('ì‚¬ìš©ì ì •ë³´ë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
      return;
    }


    try {
      const dialogRef = this.dialog.open(CreateProductDialog, {
        width: '800px',
        maxWidth: '95vw',
        maxHeight: '90vh',
        data: {
          storeId: this.storeId,
          storeName: this.store?.name,
          userId: userId
        },
        disableClose: false,
        autoFocus: true
      });


      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          // ìƒí’ˆ ëª©ë¡ ìƒˆë¡œê³ ì¹¨
          this.loadStoreProducts();
        }
      });
    } catch (error) {
      alert('ë‹¤ì´ì–¼ë¡œê·¸ë¥¼ ì—´ ìˆ˜ ì—†ìŠµë‹ˆë‹¤: ' + error);
    }
  }
