import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IOrder } from '../interfaces/models/store.interface';
import { environment } from '../config/environment';

/**
 * ì£¼ë¬¸ ì•„ì´í…œ DTO (SyncAna í˜•ì‹)
 */
export interface CreateOrderItemDto {
  productId: string;              // ìƒí’ˆ ID
  storeId: string;                // ìŠ¤í† ì–´ ID (ë¬¶ìŒë°°ì†¡Â·ì •ì‚°ìš©)
  quantity: number;               // ì£¼ë¬¸ ìˆ˜ëŸ‰
  shippingFee: number;            // ë°°ì†¡ë¹„
  productSnapshot: {              // ì£¼ë¬¸ ì‹œì  ìŠ¤ëƒ…ìƒ·
    name: string;
    unitPrice: number;
    unitDiscount: number;
    finalUnitPrice: number;
    shippingFee: number;
    thumbnailUrl?: string;
    option?: Record<string, string>;
  };
}

/**
 * ì£¼ë¬¸ ìƒì„± DTO (SyncAna í˜•ì‹)
 */
export interface CreateOrderDto {
  ownerId: string;                // êµ¬ë§¤ì ID
  items: CreateOrderItemDto[];    // ì£¼ë¬¸ ì•„ì´í…œ ë°°ì—´
  
  // ê¸ˆì•¡ ìš”ì•½
  itemsTotal: number;             // ìƒí’ˆ ê¸ˆì•¡ í•©ê³„
  discountTotal: number;          // í• ì¸ ê¸ˆì•¡ í•©ê³„
  shippingTotal: number;          // ë°°ì†¡ë¹„ í•©ê³„
  paymentTotal: number;           // ìµœì¢… ê²°ì œ ê¸ˆì•¡
  currency: 'KRW';
  
  // ê²°ì œ
  paymentMethod: 'card' | 'vbank' | 'kakaopay' | 'bankTransfer';
  
  // ì£¼ë¬¸ì ì •ë³´
  ordererName: string;
  ordererPhone: string;
  
  // ë°°ì†¡ì§€ ì •ë³´
  receiverName: string;
  receiverPhone: string;
  zipcode: string;
  address: string;
  addressDetail: string;
  deliveryMessage?: string;
  
  cashReceiptNumber?: string;     // í˜„ê¸ˆì˜ìˆ˜ì¦ ë²ˆí˜¸
}

@Injectable({
  providedIn: 'root'
})
export class OrderApiService {
  private readonly baseUrl = environment.baseSyncUrl + '/order';

  constructor(private http: HttpClient) {}

  /**
   * ì¥ë°”êµ¬ë‹ˆì—ì„œ ì£¼ë¬¸ ìƒì„±
   */
  createOrderFromCart$(orderData: CreateOrderDto): Observable<IOrder> {
    return this.http.post<IOrder>(`${this.baseUrl}/cart`, orderData).pipe(
      map(order => this.mapOrderResponse(order))
    );
  }

  /**
   * ì§ì ‘ êµ¬ë§¤ë¡œ ì£¼ë¬¸ ìƒì„±
   */
  createDirectOrder$(orderData: CreateOrderDto): Observable<IOrder> {
    return this.http.post<IOrder>(`${this.baseUrl}/direct`, orderData).pipe(
      map(order => this.mapOrderResponse(order))
    );
  }

  /**
   * ì£¼ë¬¸ ì¡°íšŒ
   */
  getOrder$(orderId: string): Observable<IOrder> {
    return this.http.get<IOrder>(`${this.baseUrl}/${orderId}`).pipe(
      map(order => this.mapOrderResponse(order))
    );
  }

  /**
   * ì‚¬ìš©ìì˜ ëª¨ë“  ì£¼ë¬¸ ì¡°íšŒ
   */
  getUserOrders$(userId: string): Observable<IOrder[]> {
    return this.http.get<IOrder[]>(`${this.baseUrl}/user/${userId}`).pipe(
      map(orders => orders.map(order => this.mapOrderResponse(order)))
    );
  }

  /**
   * ë‚´ ì£¼ë¬¸ ëª©ë¡ ì¡°íšŒ (ë¡œê·¸ì¸í•œ ì‚¬ìš©ì)
   * SyncAna ë°©ì‹: /order?ownerId={userId}
   */
  getMyOrders$(userId: string): Observable<IOrder[]> {
    const params = new HttpParams().set('ownerId', userId);
    return this.http.get<IOrder[]>(`${this.baseUrl}`, { params }).pipe(
      map(orders => (orders || []).map(order => this.mapOrderResponse(order)))
    );
  }

  /**
   * ì£¼ë¬¸ ì·¨ì†Œ
   */
  cancelOrder$(orderId: string): Observable<IOrder> {
    return this.http.patch<IOrder>(`${this.baseUrl}/${orderId}/cancel`, {}).pipe(
      map(order => this.mapOrderResponse(order))
    );
  }

  /**
   * ìŠ¤í† ì–´ë³„ ì£¼ë¬¸ ì•„ì´í…œ ì¡°íšŒ (íŒë§¤ììš©)
   */
  getStoreOrderItems$(storeId: string): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/items/store/${storeId}`).pipe(
      map(items => items.map(item => this.mapOrderItemResponse(item)))
    );
  }

  /**
   * ëª¨ë“  ì£¼ë¬¸ ì¡°íšŒ (ê´€ë¦¬ììš©)
   */
  getAllOrders$(): Observable<IOrder[]> {
    return this.http.get<IOrder[]>(`${this.baseUrl}/all`).pipe(
      map(orders => orders.map(order => this.mapOrderResponse(order)))
    );
  }

  /**
   * ì£¼ë¬¸ ìƒíƒœ ì¼ê´„ ì—…ë°ì´íŠ¸ (ë¬´í†µì¥ ì…ê¸ˆ í™•ì¸ ë“±)
   */
  updateOrdersStatus$(orderIds: string[], status: 'paid' | 'pending' | 'cancelled'): Observable<any> {
    return this.http.patch(`${this.baseUrl}/status`, { orderIds, status });
  }

  /**
   * ì£¼ë¬¸ ì•„ì´í…œ ìƒíƒœ ì—…ë°ì´íŠ¸
   */
  updateOrderItemsStatus$(orderItemIds: string[], status: string): Observable<any> {
    return this.http.patch(`${this.baseUrl}/items/status`, { orderItemIds, status });
  }

  /**
   * ì†¡ì¥ ì •ë³´ ì—…ë°ì´íŠ¸ (ë°°ì†¡ ì²˜ë¦¬)
   */
  updateOrderItemsInvoices$(data: {
    orderItemIds: string[];
    carrier: string;
    trackingNumber: string;
  }): Observable<any> {
    return this.http.patch(`${this.baseUrl}/items/invoices`, data);
  }

  /**
   * ë°±ì—”ë“œ ì‘ë‹µì„ UI ëª¨ë¸ë¡œ ë§¤í•‘
   */
  private mapOrderResponse(order: any): IOrder {
    return {
      ...order,
      createdAt: new Date(order.createdAt),
      updatedAt: order.updatedAt ? new Date(order.updatedAt) : undefined,
      items: order.items?.map((item: any) => {
        // productSnapshotì´ JSON ë¬¸ìì—´ì´ë©´ íŒŒì‹±
        let productSnapshot = item.productSnapshot;
        if (typeof productSnapshot === 'string') {
          try {
            productSnapshot = JSON.parse(productSnapshot);
          } catch (e) {
            productSnapshot = null;
          }
        }
        
        return {
          ...item,
          productSnapshot,
          createdAt: new Date(item.createdAt),
          updatedAt: item.updatedAt ? new Date(item.updatedAt) : undefined
        };
      }) || []
    };
  }

  /**
   * ì£¼ë¬¸ ì•„ì´í…œ ì‘ë‹µ ë§¤í•‘
   */
  private mapOrderItemResponse(item: any): any {
    let productSnapshot = item.productSnapshot;
    if (typeof productSnapshot === 'string') {
      try {
        productSnapshot = JSON.parse(productSnapshot);
      } catch (e) {
        productSnapshot = null;
      }
    }
    
    return {
      ...item,
      productSnapshot,
      createdAt: new Date(item.createdAt),
      updatedAt: item.updatedAt ? new Date(item.updatedAt) : undefined,
      order: item.order ? {
        ...item.order,
        createdAt: new Date(item.order.createdAt)
      } : undefined
    };
  }
}
