  uploadImages$(files: File[], userId: string): Observable<Array<{ id: string; url: string }>> {
    const formData = new FormData();
    files.forEach(file => {
      formData.append('files', file);
    });
    
    return this.http.post<Array<{ id: string; url: string }>>(
      environment.baseSyncUrl + '/upload/images',
      formData,
      {
        headers: {
          'x-user-id': userId
        }
      }
    );
  }

  // Map backend store response to UI model
  private mapStoreResponse(store: StoreResponseDto): IStore {
    return {
      ...store,
      logoImageMeta: store.logoImageMeta ? { 
        ...store.logoImageMeta, 
        url: environment.baseSyncUrl + store.logoImageMeta.url 
      } : undefined,
      productCount: store.productCount,
      createdAt: new Date(store.createdAt),
    } as IStore;
  }

