  /**
   * ì´ë¯¸ì§€ URLì—ì„œ íŒŒì¼ëª… ì¶”ì¶œ
   */
  getImageFileName(url: string): string {
    if (!url) return 'ì´ë¯¸ì§€ ì—†ìŒ';
    try {
      const urlParts = url.split('/');
      const fileName = urlParts[urlParts.length - 1];
      // URL íŒŒë¼ë¯¸í„° ì œê±°
      return fileName.split('?')[0] || 'ì´ë¯¸ì§€';
    } catch (e) {
      return 'ì´ë¯¸ì§€';
    }
  }
