  onMenuClick(menuId: string): void {
    this.selectedCategory = this.selectedCategory === menuId ? null : menuId;
    this.menuAction.emit({
      type: 'category-select',
      category: menuId,
      expanded: this.selectedCategory === menuId
    });
  }

  onSubItemClick(subItemId: string): void {
    this.menuAction.emit({
      type: 'menu-click',
      menuId: subItemId
    });
  }

  onBack(): void {
    this.selectedCategory = null;
    this.menuAction.emit({
      type: 'back'
    });
  }

  onClose(): void {
    this.close.emit();
  }

  getMenuTitle(): string {
    if (!this.selectedCategory) return '';
    const menu = this.menuItems.find(m => m.id === this.selectedCategory);
    return menu ? menu.title : '';
  }

  getMenuItems(): StoreSubMenuItem[] {
    if (!this.selectedCategory) return [];
    const menu = this.menuItems.find(m => m.id === this.selectedCategory);
    return menu ? menu.items : [];
  }

  // ì¸ì¦ ê´€ë ¨ ë©”ì„œë“œë“¤
  onLoginClick(): void {
    const currentMode = this.currentMode || 'CUSTOMER';
    switch (currentMode) {
      case 'CUSTOMER':
        this.router.navigate(['/store/login']);
        break;
      case 'SELLER':
        this.router.navigate(['/store/seller/login']);
        break;
      case 'ADMIN':
        this.router.navigate(['/store/admin/login']);
        break;
    }
    this.close.emit();
  }

  onLogout(): void {
    this.authModeService.logout().subscribe(() => {
      this.router.navigate(['/store']);
    });
    this.close.emit();
  }

  onModeSwitch(mode: UserRole): void {
    this.authModeService.switchMode(mode).subscribe(() => {
      // ëª¨ë“œ ë³€ê²½ í›„ í•´ë‹¹ ëª¨ë“œì˜ í™ˆìœ¼ë¡œ ì´ë™
      switch (mode) {
        case 'CUSTOMER':
          this.router.navigate(['/store']);
          break;
        case 'SELLER':
          this.router.navigate(['/store/seller']);
          break;
        case 'ADMIN':
          this.router.navigate(['/store/admin']);
          break;
      }
    });
    this.close.emit();
  }

  getUserDisplayName(user: IUser): string {
    if (user.role === 'SELLER') {
      const sellerUser = user as any;
      if (sellerUser.store?.name) {
        return sellerUser.store.name;
      }
    }
    return user.name || user.email;
  }

