import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, map, catchError, of } from 'rxjs';
import { environment } from '../config/environment';
import { IProduct, ISearchProductDTO, ProductSearchResponseDto } from '../interfaces/models/store.interface';

@Injectable({ providedIn: 'root' })
export class ProductApiService {
  private readonly baseUrl = environment.baseSyncUrl + '/product';

  constructor(private readonly http: HttpClient) {}

  /**
   * íŠ¹ì • ìŠ¤í† ì–´ì˜ ëª¨ë“  ìƒí’ˆ ì¡°íšŒ
   * SyncAna íŒ¨í„´: /store/:storeId/products (withReviews ì˜µì…˜)
   */
  getProductsByStoreId$(storeId: string): Observable<IProduct[]> {
    // SyncAna íŒ¨í„´: /store/:storeId/products
    const url = environment.baseSyncUrl + `/store/${storeId}/products`;
    const params = new HttpParams().set('withReviews', 'true');
    
    return this.http.get<IProduct[]>(url, { params }).pipe(
      map(products => products.map(p => this.mapProductResponse(p))),
      catchError((error) => {
        return of([]);
      })
    );
  }

  /**
   * ë‹¨ì¼ ìƒí’ˆ ì¡°íšŒ
   * SyncAna íŒ¨í„´: /store/products/:productId
   */
  getProductById$(productId: string): Observable<IProduct | null> {
    // SyncAna íŒ¨í„´: /store/products/:productId
    const url = environment.baseSyncUrl + `/store/products/${productId}`;
    
    return this.http.get<IProduct>(url).pipe(
      map(product => this.mapProductResponse(product)),
      catchError((error) => {
        return of(null);
      })
    );
  }

  /**
   * ëª¨ë“  ìƒí’ˆ ì¡°íšŒ (ê´€ë¦¬ììš©)
   * SyncAna íŒ¨í„´: /store/products/all
   */
  getAllProducts$(): Observable<IProduct[]> {
    const url = environment.baseSyncUrl + '/store/products/all';
    
    return this.http.get<IProduct[]>(url).pipe(
      map(products => products.map(p => this.mapProductResponse(p))),
      catchError((error) => {
        return of([]);
      })
    );
  }

  /**
   * ìƒí’ˆ ê²€ìƒ‰
   * ì‹¤ì œ API ê²½ë¡œ: GET /store/products/search/visible?q={query}&sort={sort}&page={page}&limit={limit}
   */
  searchProducts$(searchDto: ISearchProductDTO): Observable<ProductSearchResponseDto> {
    const params = new HttpParams({ 
      fromObject: { 
        q: searchDto.q,
        sort: searchDto.sort,
        page: searchDto.page.toString(),
        limit: searchDto.limit.toString()
      } 
    });
    
    const url = environment.baseSyncUrl + '/store/products/search/visible';
    return this.http.get<ProductSearchResponseDto>(url, { params }).pipe(
      map(response => ({
        products: response.products.map(p => this.mapProductResponse(p)),
        hasNext: response.hasNext
      })),
      catchError((error) => {
        return of({ products: [], hasNext: false });
      })
    );
  }

  /**
   * ë°±ì—”ë“œ ì‘ë‹µì„ UI ëª¨ë¸ë¡œ ë§¤í•‘
   * SyncAna íŒ¨í„´: ìƒëŒ€ ê²½ë¡œì— baseSyncUrl ë¶™ì´ê¸°
   */
  private mapProductResponse(product: any): IProduct {
    return {
      ...product,
      createdAt: product.createdAt ? new Date(product.createdAt) : new Date(),
      thumbnailMeta: product.thumbnailMeta ? {
        ...product.thumbnailMeta,
        // SyncAna íŒ¨í„´: ìƒëŒ€ ê²½ë¡œì— baseSyncUrlì„ prefixë¡œ ì¶”ê°€
        url: product.thumbnailMeta.url.startsWith('http') 
          ? product.thumbnailMeta.url 
          : `${environment.baseSyncUrl}${product.thumbnailMeta.url}`
      } : undefined,
      imageMetas: product.imageMetas?.map((img: any) => ({
        ...img,
        url: img.url.startsWith('http') 
          ? img.url 
          : `${environment.baseSyncUrl}${img.url}`
      })) || []
    } as IProduct;
  }
}
