  // ìƒí’ˆ ìƒì„±
  async openCreateProductDialog(): Promise<void> {
    this.isFabOpen = false;


    // 1. íŒë§¤ì ê¶Œí•œ í™•ì¸
    const currentMode = this.authModeService.getCurrentMode();
    const isSellerAuthenticated = this.sellerAuthService.isAuthenticated();


    if (currentMode !== 'SELLER' || !isSellerAuthenticated) {
      alert('íŒë§¤ì ëª¨ë“œë¡œ ì „í™˜í•˜ê³  ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
      return;
    }

    // 2. í˜„ì¬ ë¡œê·¸ì¸í•œ íŒë§¤ì ì •ë³´ ê°€ì ¸ì˜¤ê¸° (í† í°ì—ì„œ íŒŒì‹±)
    const token = localStorage.getItem('ez_store_seller_token');
    
    if (!token) {
      alert('íŒë§¤ì ë¡œê·¸ì¸ ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
      return;
    }

    try {
      // í† í° ë””ì½”ë”©í•˜ì—¬ userId ê°€ì ¸ì˜¤ê¸°
      let payload = JSON.parse(atob(token.split('.')[1]));
      
      // ì¤‘ì²©ëœ payload êµ¬ì¡° ì²˜ë¦¬ (payload.payload ì¡´ì¬ ì‹œ)
      if (payload.payload) {
        payload = payload.payload;
      }
      
      
      // í† í°ì— ìŠ¤í† ì–´ ì •ë³´ê°€ ìˆìœ¼ë©´ ë°”ë¡œ ì‚¬ìš©
      if (payload.store?.id) {
        this.router.navigate(['/store/detail', payload.store.id], {
          queryParams: { openProductDialog: 'true' }
        });
        return;
      }
      
      // ìŠ¤í† ì–´ ì •ë³´ê°€ ì—†ìœ¼ë©´ APIë¡œ ì¡°íšŒ
      const userId = payload.id || payload.userId || payload.sub || payload.userIdx || payload.sellerId;

      if (!userId) {
        alert('íŒë§¤ì ì •ë³´ë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
        return;
      }


      // 3. íŒë§¤ìì˜ ìŠ¤í† ì–´ ì°¾ê¸°
      this.storeApiService.getMyStore$(userId).subscribe({
        next: (store) => {
          
          if (!store) {
            alert('ë“±ë¡ëœ ìŠ¤í† ì–´ê°€ ì—†ìŠµë‹ˆë‹¤. ë¨¼ì € ë¸Œëœë“œë¥¼ ìƒì„±í•´ì£¼ì„¸ìš”.');
            return;
          }


          // 4. ìŠ¤í† ì–´ ìƒì„¸ í˜ì´ì§€ë¡œ ì´ë™í•˜ë©° openProductDialog íŒŒë¼ë¯¸í„° ì „ë‹¬
          this.router.navigate(['/store/detail', store.id], {
            queryParams: { openProductDialog: 'true' }
          });
        },
        error: (error) => {
          alert('ìŠ¤í† ì–´ ì •ë³´ë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.');
        }
      });
    } catch (error) {
      alert('ë¡œê·¸ì¸ ì •ë³´ê°€ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
    }
  }

